.timelineCard {
  position: relative;
  width: 50%;
  min-height: 120px;
}

.cardPosition-left {
  left: 0;
  padding-right: 70px;
  text-align: right;
}

.cardPosition-right {
  left: 50%;
  padding-left: 70px;
  text-align: left;
}

.cardPosition-right .card::before {
  right: -50px;
  border-width: 100px 0 100px 50px;
  border-color: transparent transparent transparent rgb(0, 0, 0);
  top: -1px;
}

.cardPosition-left .card::before {
  left: -50px;
  border-width: 100px 50px 100px 0;
  border-color: transparent rgb(255, 255, 255) transparent transparent;
  top: -1px;
}

.cardPosition-left .numberBox {
  right: 0;
  margin-right: -2rem;
}

.cardPosition-right .numberBox {
  left: 0;
  margin-left: -2.5rem;
}

