@media screen and (max-width: 767px) {
  .timeline::after {
    left: 65px;
  }
  .containerl {
    width: 100%;
    padding-left: 100px;
    padding-right: 0px;
    margin-top: 30px;
  }
  .containerl:first-of-type {
    margin-top: 0px;
  }
  .containerl-right {
    left: 0%;
  }
  .containerl-left {
    text-align: left;
  }
  .containerl .image {
    left: -10px;
    top: 0px;
    margin-left: 0;
    margin-right: 0;
  }
  .containerl-left .content::before {
    left: -10px;
    right: auto;
    border-width: 10px 10px 10px 0;
    border-color: transparent rgb(255, 255, 255) transparent transparent;
  }
  .containerl .black-box::before {
    left: 25px;
    top: -10px;
    border: medium solid rgb(0, 0, 0);
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent rgb(0, 0, 0) transparent;
  }
}
@media screen and (max-width: 480px) {
  .containerl {
    padding-left: 0px;
    padding-top: 105px;
    z-index: 3;
  }
  .containerl .content::before {
    left: 25px;
    top: -10px;
    border: medium solid rgb(255, 255, 255);
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent rgb(255, 255, 255) transparent;
  }
  .containerl .black-box::before {
    left: 25px;
    top: -10px;
    border: medium solid rgb(0, 0, 0);
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent rgb(0, 0, 0) transparent;
  }
}

